import {commonParams} from './config'
import axios from 'axios'

export function getLyric(songMID){
	const url='/api/lyric';
	const data = Object.assign({},commonParams, {
	songmid: songMID,
    platform: 'yqq',
    hostUin: 0,
    needNewCode: 0,
    categoryId: 10000000,
    pcachetime: +new Date(),
    format: 'json'
  })

  return axios.get(url, {
    params: data
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}


